
 

.profile {
	width: 250px;
	text-align: center;
	margin: auto;
	border: solid 1px #d8e0ea;
    
}

.description {
	width: 250px;
	height: auto;
}

.avatar {
	width: 128px;
	height: 128px;
	border-radius: 50%;
	overflow: hidden;
	text-align: center;
	margin: auto;
	background-color: beige;
}

.name {
	font-size: 18px;
	color: #333333;
	font-weight: 700;
	margin-top: 30px;
}

.tag {
	font-size: 14px;
	color: #666666;
	text-align: center;
	margin-top: 10px;
}

.location {
	font-size: 14px;
	color: #97a2ae;
	line-height: 32px;
	margin-top: 10px;
	margin-bottom: 30px;
}

.stats {
	display: flex;
	justify-content: space-evenly;
	list-style: none;
	background-color: #f3f6f9;
	border-radius: 0 0 5px 5px;
}

.list {
    display: flex;
    justify-content: space-evenly;
    list-style: none;
    background-color: #f3f6f9;
    border-radius: 0 0 5px 5px;
    padding: 0;
    margin: 0;
}
.label {
	color: #97a2ae;
	padding-top: 22px;
	font-size: 12px;
	display: flex;
}

.quantity {
	padding-bottom: 22px;
	line-height: 1.2;
	font-weight: bold;
	font-size: 14px;
	display: flex;
}
 

